import React, { Component } from 'react'
import {Link} from 'react-router-dom' 
import {Navbar} from '../styled/PagesStyle'
// import styled component
// import Style
import './../styled/styleLogin.css'
import './headerStyle.css'

// -------------------------------- work 

export default class Header extends Component {
    render(){
        const color= this.props.color
        console.log(color)
        return(
            <header clsssName="Header-Section">
                <Navbar color={color}>
                    <ul className="ListLink">
                        <Link className="LinkBar" to="/"><i className="Link"></i></Link>
                        <Link className="LinkBar hidTab" to="/About-Academy"><i className="Link"> </i></Link>
                        <Link className="LinkBar hidTab"><i className="Link dropdown">
                              
                             <ul className="dropdown-menu">
                            <Link className="DropDownItem"><i className="itemlist"></i></Link>
                            <Link className="DropDownItem"><i className="itemlist"></i></Link>
                            <Link className="DropDownItem"><i className="itemlist"></i></Link>
                            </ul>
                             </i></Link>

                    </ul>
                </Navbar>
            </header>
        )
    }
}

